<template>

  <div>
    <h3>工位信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :model="formData" :label-width="80">
          <FormItem label="工位编号">
            <Input v-model="formData.workplace_no" placeholder="请填写工位编号"></Input>
          </FormItem>

          <FormItem label="工位类型">
            <Select v-model="formData.type">
              <Option label="按工位租赁" :value="0"></Option>
              <Option label="按面积租赁" :value="1"></Option>
            </Select>
          </FormItem>

          <FormItem label="总面积" v-show="formData.type===1">
            <InputNumber
              style="width: 100%"
              v-model="formData.total_area">
            </InputNumber>
          </FormItem>
          <FormItem label="所属空间">
            <Select v-model="formData.space_id">
              <Option v-for="(item,index) in space" v-bind:key="index" :label="item.name"
                      :value="item.id"></Option>
            </Select>
          </FormItem>

          <FormItem label="工位分组">
            <Select v-model="formData.group_id">
              <Option v-for="(item,index) in groups" v-bind:key="index" :label="item.name"
                      :value="item.id"></Option>
            </Select>
          </FormItem>

          <FormItem label="价格">
            <InputNumber
              style="width: 100%"
              v-model="formData.price">
            </InputNumber>
          </FormItem>

          <FormItem label="状态">
            <Select v-model="formData.status">
              <Option label="未出租" :value="0"></Option>
              <Option label="已出租" :value="1"></Option>
            </Select>
          </FormItem>

          <FormItem label="到期时间">
            <DatePicker style="width: 100%" type="date" v-model="formData.deadline" placeholder="选择时间"></DatePicker>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'
import groupApi from './group/api'
import spaceApi from '../space/api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {
        price: 0,
        total_area: 0
      },
      groups: [],
      space: [],
      load: false
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
    this.loadGroup()
    this.loadSpace()
  },
  methods: {
    loadGroup () {
      groupApi.select(res => {
        this.groups = res.data.data
      })
    },
    loadSpace () {
      spaceApi.select(res => {
        this.space = res.data.data
      })
    },
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data.workplace
        this.formData.price = parseFloat(this.formData.price)
        if (this.formData.total_area) {
          this.formData.total_area = parseFloat(this.formData.total_area)
        }
      }, () => {
        this.load = false
      })
    },
    submit () {
      this.load = true
      api.save(this.formData, res => {
        this.load = false
        this.$router.push({ name: 'workplace' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

